<template>
  <div class="container">
    <div class="nullBasicInfo" v-if="!isComplete">
      <el-alert title="暂无信息" type="warning" show-icon :closable="false"></el-alert>
      <el-button type="primary" class="entranceBasicInfo" @click="showBasicInfoDialog()">完善个人基本信息</el-button>
    </div>
    <div class="basicInfo" v-else>
      <div class="leftInfo">
        <p>
          <span>姓名:</span>
          <span>{{stuInfo.stuName}}</span>
        </p>
        <p>
          <span>学号:</span>
          <span>{{stuInfo.stuNo}}</span>
        </p>
        <p>
          <span>学历:</span>
          <span v-if="stuInfo.education==1">本科</span>
          <span v-if="stuInfo.education==2">博士</span>
          <span v-if="stuInfo.education==3">硕士</span>
          <span v-if="stuInfo.education==4">高中</span>
        </p>
        <p>
          <span>班级:</span>
          <span>{{stuInfo.className}}</span>
        </p>
        <p>
          <span>政治面貌:</span>
          <span v-if="stuInfo.politicalStatus==1">党员</span>
          <span v-if="stuInfo.politicalStatus==2">预备党员</span>
          <span v-if="stuInfo.politicalStatus==3">共青团员</span>
          <span v-if="stuInfo.politicalStatus==4">群众</span>
        </p>
        <p>
          <span>毕业时间:</span>
          <span>{{stuInfo.graduationTime}}</span>
        </p>
        <p>
          <span>生源地:</span>
          <span>{{stuInfo.biogenicLand}}</span>
        </p>
        <p>
          <span>联系电话:</span>
          <span>{{stuInfo.stuTel}}</span>
        </p>
      </div>
      <div class="rightInfo">
        <p>
          <span>性别:</span>
          <span>{{stuInfo.stuSex == "1" ? "男" : "女"}}</span>
        </p>
        <p>
          <span>身份证:</span>
          <span>{{stuInfo.idCard}}</span>
        </p>
        <p>
          <span>院系:</span>
          <span>{{stuInfo.departmentName}}</span>
        </p>
        <p>
          <span>专业:</span>
          <span>{{stuInfo.professionName}}</span>
        </p>

        <p>
          <span>家庭联系人:</span>
          <span>{{stuInfo.familyContact}}</span>
        </p>
        <p>
          <span>家庭联系电话:</span>
          <span>{{stuInfo.homeTel}}</span>
        </p>
        <p>
          <span>家庭地址:</span>
          <span>{{stuInfo.homeAddress}}</span>
        </p>
      </div>
      <div class="modify">
        <el-button type="primary" @click="dialogFormVisible = true">修改</el-button>
      </div>
    </div>
    <div class="dialogBox">
      <el-dialog
        :title="isComplete ?'修改基本信息':'完善基本信息'"
        :visible.sync="dialogFormVisible"
        width="60%"
      >
        <el-form :model="stuInfo">
          <el-form-item label="学号" :label-width="formLabelWidth">
            <el-input v-model="stuInfo.stuNo" autocomplete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="姓名" :label-width="formLabelWidth">
            <el-input v-model="stuInfo.stuName" autocomplete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="身份证" :label-width="formLabelWidth">
            <el-input v-model="stuInfo.idCard" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="性别" :label-width="formLabelWidth">
            <el-select v-model="stuInfo.stuSex" placeholder="请选择你的性别">
              <el-option label="男" value="1"></el-option>
              <el-option label="女" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="政治面貌" :label-width="formLabelWidth">
            <el-select v-model="stuInfo.politicalStatus" placeholder="请选择你的政治面貌">
              <el-option label="党员" value="1"></el-option>
              <el-option label="预备党员" value="2"></el-option>
              <el-option label="共青团员" value="3"></el-option>
              <el-option label="群众" value="4"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="学历" :label-width="formLabelWidth">
            <el-select v-model="stuInfo.education" placeholder="请选择你的学历">
              <el-option label="本科" value="1"></el-option>
              <el-option label="博士" value="2"></el-option>
              <el-option label="硕士" value="3"></el-option>
              <el-option label="高中" value="4"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="联系电话" :label-width="formLabelWidth">
            <el-input v-model="stuInfo.stuTel" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="院系" :label-width="formLabelWidth">
            <el-select v-model="stuInfo.departmentKey" placeholder="请选择你的学院" @change="changeDepart">
              <el-option
                v-for="(item,index) in departmentList"
                :key="index"
                :label="item.name"
                :value="item.key"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="专业" :label-width="formLabelWidth">
            <el-select v-model="stuInfo.professionKey" placeholder="请选择你的专业" @change="changeProfssion">
              <el-option
                v-for="(item,index) in professionList"
                :key="index"
                :label="item.name"
                :value="item.key"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="班级" :label-width="formLabelWidth">
            <el-select v-model="stuInfo.classNo" placeholder="请选择你的班级">
              <el-option
                v-for="(item,index) in classList"
                :key="index"
                :label="item.name"
                :value="item.key"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="毕业时间" :label-width="formLabelWidth">
            <el-date-picker
              v-model="stuInfo.graduationTime"
              type="date"
              placeholder="选择毕业时间"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="生源地" :label-width="formLabelWidth">
            <el-input v-model="stuInfo.biogenicLand" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="家庭联系人" :label-width="formLabelWidth">
            <el-input v-model="stuInfo.familyContact" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="家庭联系电话" :label-width="formLabelWidth">
            <el-input v-model="stuInfo.homeTel" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="家庭住址" :label-width="formLabelWidth">
            <el-input v-model="stuInfo.homeAddress" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="isComplete ? updateStuInfo() : addBasicInfo()">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  interStuInfo,
  updateStuInfo,
  addStuInfo,
  getAllClassInfo,
  getProfessionAttach
} from "../../../../../utils/employment.url";

export default {
  data() {
    return {
      userInfo: {},
      stuInfo: {},
      dialogFormVisible: false,
      formLabelWidth: "120px",
      isComplete: true, //信息是否完全
      departmentList: [], //学院列表
      professionList: [], //专业列表
      classList: [], //班级列表
      attachList: [] //院系信息列表
    };
  },
  methods: {
    //获取班级信息
    // getClassList() {
    //   getAllClassInfo({}, "get").then(res => {
    //     if (res.status == 200) {
    //       this.classList = res.data;
    //     }
    //   });
    // },
    //获取学生基本信息
    getStuInfo() {
      interStuInfo(
        {
          stuNo: this.userInfo.userId
        },
        "get"
      )
        .then(res => {
          if (res.status == 200) {
            this.isComplete = true;
            this.stuInfo = res.data;
            this.stuInfo.graduationTime = this.stuInfo.graduationTime.split(
              "T"
            )[0];
          }
          if (res.status == 401) {
            this.isComplete = false;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //修改学生基本信息
    updateStuInfo() {
      updateStuInfo(this.stuInfo, "put").then(res => {
        this.dialogFormVisible = false;
        if (res.status == 200) {
          this.$message({
            message: res.message,
            type: "success"
          });
          this.getStuInfo();
        }
      });
    },
    //添加学生基本信息
    addBasicInfo() {
      addStuInfo(this.stuInfo, "post").then(res => {
        if (res.status == 200) {
          this.dialogFormVisible = false;
          this.getStuInfo();
        }
      });
    },
    //显示弹框完善信息
    showBasicInfoDialog() {
      this.stuInfo.stuName = this.userInfo.userName;
      this.stuInfo.stuNo = this.userInfo.userId;
      this.dialogFormVisible = true;
    },
    getProfessionAttach(session, departmentKey, professionKey, classKey) {
      var params = {
        session: session ? session : "",
        departmentKey: departmentKey ? departmentKey : "",
        professionKey: professionKey ? professionKey : "",
        classKey: classKey ? classKey : ""
      };
      getProfessionAttach(params, "get").then(res => {
        if (res.status == 200) {
          this.attachList = res.data;
          // this.departmentList = this.groupBy(
          //   this.attachList,
          //   "deparymentKey",
          //   "departmentName"
          // );
          this.departmentList = this.groupBy(this.attachList, function(item) {
            return [item.departmentKey, item.departmentName]; //按照时间年月分组
          });
        }
      });
    },
    //数组对象分组
    // groupBy(arrList, key, name) {
    //   var map = {},
    //     dest = [];
    //   for (var i = 0; i < arrList.length; i++) {
    //     var ai = arrList[i];
    //     if (!map[ai.key]) {
    //       dest.push({
    //         key: ai.key,
    //         name: ai.name,
    //         data: [ai]
    //       });
    //       map[ai.key] = ai;
    //     } else {
    //       for (var j = 0; j < dest.length; j++) {
    //         var dj = dest[j];
    //         if (dj.key == ai.key) {
    //           dj.data.push(ai);
    //           break;
    //         }
    //       }
    //     }
    //   }
    //   return dest;
    // }

    //数组对象分组
    groupBy(array, f) {
      var groups = {};
      array.forEach(function(o) {
        var group = JSON.stringify(f(o));
        groups[group] = groups[group] || [];
        groups[group].push(o);
      });
      return Object.keys(groups).map(function(group) {
        var _group =JSON.parse(group)
        return {
          key:_group[0],
          name:_group[1],
          child:groups[group]
        };
      });
    },
    changeDepart(key){
      this.stuInfo.professionKey='',
      this.departmentList.map((item,index) =>{
        if(item.key == key){
          this.professionList = this.groupBy(item.child, function(item) {
            return [item.professionKey, item.professionName]; //按照时间年月分组
          });
          // console.log(this.professionList)
        }
      })
    },
    changeProfssion(key){
      // this.stuInfo.classNo='',
      this.professionList.map((item,index) =>{
        if(item.key == key){
          this.classList = this.groupBy(item.child, function(item) {
            return [item.classKey, item.className]; //按照时间年月分组
          });
        }
      })
    }
  },
  computed: {},
  watch: {},
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    this.getStuInfo(); //获取学生的基本信息
    let session = this.userInfo.session ? this.userInfo.session : "";
    let departmentKey = this.stuInfo.departmentKey
      ? this.stuInfo.departmentKey
      : "";
    let professionKey = this.stuInfo.professionKey
      ? this.stuInfo.professionKey
      : "";
    let classKey = this.stuInfo.classKey ? this.stuInfo.classKey : "";
    this.getProfessionAttach(session, departmentKey, professionKey, classKey);
    // this.getClassList();
  }
};
</script>
<style lang="scss" scoped>
.basicInfo {
  .leftInfo,
  .rightInfo {
    display: inline-block;
    width: 50%;
    :nth-of-type(n) {
      margin: 10px;
    }
    p {
      font-family: "华文新魏";
      :nth-of-type(2n + 1) {
        font-size: 16px;
        font-weight: 600;
      }
      :nth-of-type(2n) {
        color: #999595;
      }
    }
  }
  .modify {
    margin: 20px;
    text-align: right;
  }
}
.dialogBox {
  .el-dialog {
    .el-form {
      .el-form-item {
        display: inline-block;
        width: 50%;
        .el-input {
          width: 217px;
        }
      }
    }
  }
}
.nullBasicInfo {
  text-align: center;
  .el-alert {
    width: 200px;
    padding-left: 50px;
  }
  .el-alert--warning {
    background-color: #f8f8f800;
  }
  .entranceBasicInfo {
    margin: 20px auto;
  }
}
</style>
