<template>
  <div class="employInfo">
    <div class="employBox" v-if="Object.keys(employInfo).length!=0">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>我的就业信息</span>
          <el-button
            style="float: right; padding: 3px 0"
            type="text"
            @click="showEmployDialog('update')"
          >修改</el-button>
        </div>
        <div class="employList">
          <p>
            <span>姓名:</span>
            <span>{{userInfo.userName}}</span>
          </p>
          <p>
            <span>学号:</span>
            <span>{{employInfo.stuNo}}</span>
          </p>
          <p>
            <span>就业单位:</span>
            <span>{{employInfo.unitName}}</span>
          </p>
          <p>
            <span>单位社会统一编码:</span>
            <span>{{employInfo.unifieSocialCreditCode}}</span>
          </p>
          <p>
            <span>工作地点:</span>
            <span>{{employInfo.workPlace}}</span>
          </p>
          <p>
            <span>就业岗位:</span>
            <span>{{employInfo.jobName}}</span>
          </p>
          <p>
            <span>薪酬待遇:</span>
            <span>{{employInfo.pay}}</span>
          </p>
          <p>
            <span>签约时间:</span>
            <span>{{employInfo.contractTime}}</span>
          </p>
          <p>
            <span>单位联系人:</span>
            <span>{{employInfo.unitContactor}}</span>
          </p>
          <p>
            <span>单位联系电话</span>
            <span>{{employInfo.unitTel}}</span>
          </p>
        </div>
      </el-card>
    </div>
    <div class="nullEmploy" v-else>
      <el-alert title="暂无就业信息" type="warning" show-icon :closable="false"></el-alert>
      <el-button type="primary" class="entranceEmploy" @click="showEmployDialog('addNew')">录入就业信息</el-button>
    </div>
    <div class="dialogBox">
      <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible">
        <el-form :model="employForm">
          <el-form-item label="学号" :label-width="formLabelWidth">
            <el-input v-model="employForm.stuNo" autocomplete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="姓名" :label-width="formLabelWidth">
            <el-input v-model="employForm.stuName" autocomplete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="就业岗位" :label-width="formLabelWidth">
            <el-input v-model="employForm.jobName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="就业单位" :label-width="formLabelWidth">
            <el-input v-model="employForm.unitName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="单位社会统一编码" :label-width="formLabelWidth">
            <el-input v-model="employForm.unifieSocialCreditCode" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="签约时间" :label-width="formLabelWidth">
            <el-date-picker
              v-model="employForm.contractTime"
              type="date"
              placeholder="选择签约时间"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="薪酬待遇" :label-width="formLabelWidth">
            <el-input v-model="employForm.pay" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="工作地点" :label-width="formLabelWidth">
            <el-input v-model="employForm.workPlace" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="单位联系人" :label-width="formLabelWidth">
            <el-input v-model="employForm.unitContactor" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="单位联系电话" :label-width="formLabelWidth">
            <el-input v-model="employForm.unitTel" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitChange">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  interEmployInfo,
  addEmployInfo,
  updateEmployInfo
  // addStatistics
} from "../../../../../utils/employment.url";
export default {
  data() {
    return {
      userInfo: {},
      // 就业信息
      employInfo: {},
      // 就业信息表单
      employForm: {},
      isUpdate: true,
      dialogTitle: "添加就业信息",
      dialogFormVisible: false,
      formLabelWidth: "140px"
    };
  },
  methods: {
    //获取就业信息
    getEmployInfo() {
      interEmployInfo(
        {
          stuNo: this.userInfo.userId
        },
        "get"
      ).then(res => {
        if (res.status == 200) {
          this.employInfo = res.data;
          this.employInfo.contractTime = this.employInfo.contractTime.split(
            "T"
          )[0];
        }
      });
    },
    //添加就业信息
    addEmployInfo() {
      addEmployInfo(this.employForm, "post").then(res => {
        if (res.status == 200) {
          this.dialogFormVisible = false;
          this.getEmployInfo();
        }
      });
    },
    //修改就业信息
    updateEmployInfo() {
      updateEmployInfo(this.employInfo, "put").then(res => {
        if (res.status == 200) {
          this.dialogFormVisible = false;
          this.$message({
            message: res.message,
            type: "success"
          });
          this.getEmployInfo();
        }
      });
    },
    showEmployDialog(val) {
      this.dialogFormVisible = true;
      if (val == "update") {
        this.employForm = this.employInfo;
        this.employForm.stuName = this.userInfo.userName;
        this.isUpdate = true;
        this.dialogTitle = "修改业信息";
      } else {
        this.employForm.stuNo = this.userInfo.userId;
        this.employForm.stuName = this.userInfo.userName;
        this.isUpdate = false;
        this.dialogTitle = "添加就业信息";
      }
    },
    submitChange() {
      if (this.isUpdate) {
        this.updateEmployInfo();
      } else {
        this.addEmployInfo();
      }
    }
  },
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    this.getEmployInfo();
  }
};
</script>
<style lang="scss" scoped>
.employInfo {
  .employBox {
    margin-bottom: 50px;
    .employList {
      :nth-of-type(n) {
        margin: 10px;
      }
      p {
        font-family: "华文新魏";
        :nth-of-type(2n + 1) {
          font-size: 16px;
          font-weight: 600;
        }
        :nth-of-type(2n) {
          color: #999595;
        }
      }
    }
  }
  .nullEmploy {
    text-align: center;
    .el-alert {
      width: 200px;
      padding-left: 50px;
    }
    .el-alert--warning {
      background-color: #f8f8f800;
    }
    .entranceEmploy {
      margin: 20px auto;
    }
  }
}
</style>
