import BasicModel from './stuModel/BasicModel.vue'
import EmployModel from './stuModel/EmployModel.vue'
import {
  updateUserImage,
  getByUserId
} from "../../../../utils/employment.url";
import {
  createDecipher
} from 'crypto';
export default {
  components: {
    BasicModel,
    EmployModel
  },
  data() {
    return {
      activeName: 'first',
      defaultSrc: require("../../../../assets/img/userInfo.png"),
      userInfo: {},
      fileList: []
    };
  },
  methods: {
    handleClick(tab, event) {},
    onfilechange: function (e) { //获取到图片文件
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.fileList = files[0];
      var formdata = new FormData();
      formdata.append("avatar",this.fileList)
      formdata.append("userId",this.userInfo.userId)
      updateUserImage(formdata,"post").then(res =>{
        if(res.status == 200){
          getByUserId(this.userInfo,"get").then(res =>{
            if(res.status == 200){
              sessionStorage.setItem("userInfo", JSON.stringify(res.data));
              this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
            }
          })
        }
      })

    }
  },
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
  }
};